.skills {
    padding: 100px 0;
    height: 100%;

    background-color: cadetblue;
}

.skills h2 {
    background-color: bisque;
}

.skills_list {
    margin: 0;
    padding: 0;

    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    list-style: none;
}

@media only screen and (max-width: 600px) {
    .skills {
        padding: 30px 20px;
        outline: 2px solid green;
    }

    .skills_list {
        margin: 0;
        padding: 0;

        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        flex-wrap: wrap;

        list-style: none;
    }
}